@font-face {
  font-family: 'Roboto';
  src: url('../fonts/subset-Roboto-Regular.woff2') format('woff2'), url('../fonts/subset-Roboto-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Cormorant';
  src: url('../fonts/subset-Cormorant-Bold.woff2') format('woff2'), url('../fonts/subset-Cormorant-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Montserrat';
  src: url('../fonts/subset-Montserrat-Regular.woff2') format('woff2'), url('../fonts/subset-Montserrat-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.wrapper {
  width: 90%;
  min-width: 290px;
  max-width: 700px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .wrapper {
    max-width: 1070px;
  }
}
.wrapper-reset {
  width: auto;
  min-width: 0;
  max-width: none;
  margin: 0;
  padding: 0;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  color: inherit;
  text-decoration: none;
}
ul {
  list-style: none;
}
input[type="radio"],
input[type="checkbox"],
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.hide-mobile {
  display: none;
}
@media (min-width: 768px) {
  .hide-mobile {
    display: block;
  }
}
.about__title {
  width: 90%;
  min-width: 290px;
  max-width: 700px;
  margin: 0 auto;
  font-family: "Cormorant", Times, serif;
  font-size: 1.875rem;
  line-height: 2.27rem;
  color: #464343;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 31px;
}
@media (min-width: 768px) {
  .about__title {
    max-width: 1070px;
  }
}
.about__content {
  background-color: #ffe4e4;
  padding-top: 41px;
  padding-bottom: 42px;
}
.about__description {
  width: 90%;
  min-width: 290px;
  max-width: 700px;
  margin: 0 auto;
  font-family: "Roboto", Helvetica, sans-serif;
  color: #494948;
  font-size: 0.75rem;
  line-height: 0.94rem;
}
@media (min-width: 768px) {
  .about__description {
    max-width: 1070px;
  }
}
.about__img {
  display: none;
}
@media (min-width: 768px) {
  .about__title,
  .about__description {
    width: auto;
    min-width: 0;
    max-width: none;
    margin: 0;
    padding: 0;
  }
  .about__title {
    font-size: 4.375rem;
    line-height: 5.3rem;
    padding-top: 100px;
    padding-bottom: 0;
    position: relative;
  }
  .about__title::before {
    content: "";
    height: 20px;
    display: block;
    border-top: 5px solid #b3d8e2;
    border-bottom: 5px solid #ececec;
    position: absolute;
    top: 145px;
    left: 0;
    right: calc(50% + 145px);
    margin: 0 auto;
    z-index: -1;
  }
  .about__wrapper {
    width: 90%;
    min-width: 290px;
    max-width: 700px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: -4rem;
  }
  .about__content {
    background-color: #ffffff;
  }
  .about__description {
    background-color: #ffe4e4;
  }
  .about__image {
    margin-right: -115px;
    width: 512px;
    height: auto;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .about__wrapper {
    max-width: 1070px;
  }
}
.btn {
  padding: 19px;
  font-family: "Roboto", Helvetica, sans-serif;
  color: #ffffff;
  display: block;
  text-align: center;
  max-width: 377px;
}
.btn--primary {
  background-color: #e46d71;
}
.btn--reverse {
  background-color: #ffffff;
  color: #e46d71;
  border: 1px solid #e46d71;
  font-family: "Montserrat", Helvetica, sans-serif;
}
@media (min-width: 768px) {
  .btn--primary {
    font-size: 1.25rem;
    line-height: 1.465rem;
  }
}
.contacts {
  background-color: #ffe4e4;
}
.contacts__title {
  font-family: "Cormorant", Times, serif;
  font-size: 1.875rem;
  line-height: 2.27rem;
  color: #464343;
  padding-top: 50px;
  padding-bottom: 30px;
  text-align: center;
  background-color: #ffffff;
}
.contacts__list {
  width: 90%;
  min-width: 290px;
  max-width: 700px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 34px;
  padding-bottom: 33px;
}
@media (min-width: 768px) {
  .contacts__list {
    max-width: 1070px;
  }
}
.contacts__item {
  margin-left: 25%;
  font-family: "Roboto", Helvetica, sans-serif;
  color: #494948;
  font-size: 0.75rem;
  line-height: 0.88rem;
}
.contacts__item--phone {
  margin-bottom: 20px;
}
.contacts__item--insta {
  margin-bottom: 21px;
}
.contacts__link {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.contacts__link > span {
  margin-left: 10px;
}
@media (min-width: 768px) {
  .contacts__title {
    font-size: 4.375rem;
    line-height: 5.3rem;
    padding-top: 100px;
    padding-bottom: 0;
    position: relative;
  }
  .contacts__title::before {
    content: "";
    height: 20px;
    display: block;
    border-top: 5px solid #b3d8e2;
    border-bottom: 5px solid #ececec;
    position: absolute;
    top: 145px;
    left: 0;
    right: calc(50% + 145px);
    margin: 0 auto;
    z-index: -1;
  }
}
.main-nav {
  font-family: "Montserrat", Helvetica, sans-serif;
  color: #494948;
}
.main-nav--closed {
  display: none;
}
.main-nav__list-item {
  font-size: 0.75rem;
  line-height: 0.91rem;
  text-transform: uppercase;
  text-align: center;
}
.main-nav__list-item > a {
  transition: all 0.2s ease;
}
.main-nav__list-item > a:hover,
.main-nav__list-item > a:focus {
  opacity: 0.6;
}
.main-nav__list-item > a:active {
  opacity: 0.3;
}
.main-nav--nojs {
  display: block;
}
.page-footer__wrapper {
  width: 90%;
  min-width: 290px;
  max-width: 700px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 4px;
  padding-bottom: 20px;
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    max-width: 1070px;
  }
}
.page-footer__nav {
  display: none;
}
.page-footer__contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.page-footer__contacts-first {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.page-footer__phone,
.page-footer__email {
  font-family: "Montserrat", Helvetica, sans-serif;
  color: #494948;
  font-size: 0.75rem;
  line-height: 0.91rem;
}
.page-footer__insta {
  margin-left: 8px;
}
.page-header__wrapper {
  width: 90%;
  min-width: 290px;
  max-width: 700px;
  margin: 0 auto;
  padding-top: 4px;
  padding-bottom: 2px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    max-width: 1070px;
  }
}
.page-header__contacts {
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}
.page-header__phone {
  font-family: "Montserrat", Helvetica, sans-serif;
  color: #494948;
  font-size: 0.75rem;
  line-height: 0.91rem;
}
.page-header__insta {
  margin: 0 10px;
}
.page-header__order {
  font-size: 0.75rem;
  line-height: 0.91rem;
  padding: 5px 8px;
}
.page-header__nav-toggle {
  width: 18px;
  height: 16px;
  border: none;
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  margin-left: 10px;
}
.page-header__nav-toggle--closed {
  background-image: url("../img/icon-mobile-menu.svg");
  background-size: 18px 10px;
}
.page-header__nav-toggle--opened {
  background-image: url("../img/icon-close.svg");
  background-size: 16px 16px;
}
.promo {
  padding: 78px 0 119px;
  background-image: url('../img/promo-bg-mobile.png');
  background-position: center;
  background-size: cover;
}
.promo__wrapper {
  width: 90%;
  min-width: 290px;
  max-width: 700px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .promo__wrapper {
    max-width: 1070px;
  }
}
.promo__title {
  color: #ffffff;
  font-family: "Cormorant", Times, serif;
  font-weight: normal;
  font-size: 2.5rem;
  line-height: 2.5rem;
  margin: 0;
}
.promo__description {
  color: #ffffff;
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 0.75rem;
  line-height: 0.94rem;
  margin-top: 20px;
  margin-bottom: 50px;
}
@media (min-width: 768px) {
  .promo {
    height: 985px;
    background-image: url('../img/promo-bg-desktop.png');
  }
  .promo__title {
    font-size: 7.5rem;
    line-height: 5.31rem;
  }
  .promo__description {
    font-size: 1.875rem;
    line-height: 2.5rem;
    margin-top: 31px;
    margin-bottom: 100px;
  }
}
